import React, { useState, useEffect } from 'react';
import Web3 from 'web3';
import Header from '../../components/header';
// import Footer from '../../components/footer';
import red from '../../assets/images/red-el.svg';
import green from '../../assets/images/green-el.svg';
import blue_crystall from '../../assets/images/blue-crystall-el.svg';
import OfficialPartners from '../../components/official-partners';
import mint_monkey from '../../assets/images/aos-3d-art-whitelist.png'; 
import mint_monkey_2x from '../../assets/images/aos-3d-art-whitelist@2x.png';
import { getAccounts, mintApes } from '../../utils/web3';
import { mainnetContract, aosContract, aosAbi, abi } from '../../constants/contract';

import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
import CoinbaseWalletSDK from "@coinbase/wallet-sdk";

function Mint() {
	const [availableApes, setAvailableApes] = useState(0);
	const [countApes, setCountApes] = useState('1');
	const [mintPrice, setMintPrice] = useState('0.1');

	// const web3 = new Web3(
	// 	new Web3.providers.HttpProvider(
	// 		// "https://rinkeby.infura.io/v3/2d4f413e764740ef89d5596033efec3f"
	// 		'https://mainnet.infura.io/v3/98ece994f154497b83e67c270eaf8381'
	// 	)
	// );

	const providerOptions = {
    walletconnect: {
      package: WalletConnectProvider,
      options: {
				appName: "Apes of Space",
        infuraId: "2d4f413e764740ef89d5596033efec3f",
      }
    },
		walletlink: {
			package: CoinbaseWalletSDK, 
			options: {
				appName: "Apes of Space",
				infuraId: "2d4f413e764740ef89d5596033efec3f"
			}
		},
  };
	let web3Modal
	// const aos = new web3.eth.Contract(aosAbi, aosContract);

	useEffect(
		async () => {
			web3Modal = new Web3Modal({
				network: "mainnet",
				cacheProvider: false, // optional
				providerOptions, // required
				disableInjectedProvider: false, // optional. For MetaMask / Brave / Opera.
				theme: {
					background: "#0f1315",
					main: "rgb(199, 199, 199)",
					secondary: "rgb(136, 136, 136)",
					border: "1px solid #10fdff",
					hover: "rgb(16, 26, 33)",
					borderRadius: "2px"
				}
			});

			const provider = await web3Modal.connect();
			const web3 = new Web3(provider);

			// const aos = new web3.eth.Contract(aosAbi, aosContract);

			// const account = (await getAccounts())[0];
			// if (!account) return;

			// console.log(account);

			// const apesPrice = await aos.methods.apesPrice().call(); // TOO MUCH
			// const maxApesCount = await aos.methods.MAX_APES().call();

			// setMintPrice(apesPrice); // TOO MUCH
			// setAvailableApes(maxApesCount);

			// All methods are in aos.methods, all parameters are in aosAbi.json
			// console.log(aos.methods);
		},[]
	);

	// Event handlers
	const handleMinusClick = () => {
		const countApesNumber = Number(countApes);

		if (countApesNumber <= 0) {
			setCountApes('0');
			return;
		}

		setCountApes(prev => {
			const prevNumber = Number(prev);

			return String(prevNumber - 1);
		});
	};

	const handlePlusClick = () => {
		const countApesNumber = Number(countApes);
		const availableApesNumber = Number(availableApes);

		if (countApesNumber > availableApesNumber) {
			setCountApes(availableApes);
			return;
		}

		setCountApes(prev => {
			const prevNumber = Number(prev);

			return String(prevNumber + 1);
		});
	};

	const handleInputChange = e => {
		const value = e.target.value;
		const valueNum = Number(value);
		const availableApesNumber = Number(availableApes);

		// Special requirements
		if (value.startsWith('0') && value.length >= 2) {
			setCountApes(value.slice(1));
			return;
		} else if (valueNum <= 0) {
			setCountApes('0');
			return;
		} else if (valueNum > availableApesNumber) {
			setCountApes(availableApes);
			return;
		}

		setCountApes(value);
	};

	const handleMintBtn = () => {
		const apesNum = Number(countApes);

		if(apesNum === 0) {
			return;
		}
		
		mintApes(apesNum);
	};

	return(
		<div className="mint">
			<Header />
			<div className="mint-content">
				<div className="mint-content__wrapper">
					<img data-img="red-1" src={red} alt="" />
					<img data-img="blue-1" src={blue_crystall} alt="" />
					<img data-img="mint-monkey" src={mint_monkey} srcSet={`${mint_monkey} 1x, ${mint_monkey_2x} 2x`} alt="" />
					<div className="mint-content__block">
						<img data-img="green-1" src={green} alt="" />
						<img data-img="blue-2" src={blue_crystall} alt="" />
						<h1 className="title title_size-l mint-content__title">Maximum apes of space vx available for claim: {availableApes}</h1>
						<p className="title title_size-m mint-content__subtitle">Mint price: {mintPrice} ETH</p>
						<div className="mint-content__count">
							<button type="button" className="btn btn_blue mint-content__count-btn btn-minus" onClick={handleMinusClick}>
								<span></span>
							</button>
							<input 
								type="number" 
								className="mint-input mint-content__count-input" 
								name="apes_count" 
								value={countApes}
								onChange={evt => handleInputChange(evt)}
							></input>
							<button type="button" className="btn btn_blue mint-content__count-btn btn-plus" onClick={handlePlusClick}>
								<span></span>
								<span></span>
							</button>
						</div>
						<div className="mint-content__btn-wrapper">
							<button type="button" className="btn btn_yellow mint-content__btn" 				onClick={handleMintBtn}>
								Mint aos vx
							</button>
						</div>
						<div className="mint-content__note">
							<p>
								There are many variations of passages of Lorem Ipsum available, but the majority have 
								suffered alteration in some form, by&nbsp;injected humour, or randomised words which don't 
								look even slightly believable. If you are going to&nbsp;use&nbsp;a&nbsp;passage&nbsp;of&nbsp;Lorem&nbsp;Ipsum.
							</p>
						</div>
					</div>
				</div>
			</div>
			<OfficialPartners />
		</div>
	);
}

export default Mint;


