import Web3 from "web3";
import { aosAbi, abi, aosContract, mainnetContract } from "../constants/contract";

export async function getAccounts() {
  if (!window.ethereum) return;
  // checkNetwork()
  try {
    let acc = await window.ethereum?.request({
      method: "eth_requestAccounts",
    });

    return acc;
  } catch (e) {
    return [];
  }
}

const checkNetwork = async () => {
  if (window?.ethereum) {
    try {
      await window?.ethereum?.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId: '0x1' }]
      })
      return true
    } catch (error) {
      console.error(error)
      return false
    }
  } else {
    console.error("Can't setup the network on metamask because window.ethereum is undefined")
    return false
  }
}

export async function connectMetamask() {
  if (window.ethereum) {
    try {
      const result = await getAccounts();
      if (Array.isArray(result) && result.length > 0) {
        let acc = result[0];
        return acc;
      } else {
        return false;
      }
    } catch (err) {
      return false;
    }
  } else {
    return false;
  }
}

export async function claimLady(startIndex, numberToClaim) {
  if (!window.ethereum) return;
  var web3 = new Web3(
    new Web3.providers.HttpProvider(
      // "https://rinkeby.infura.io/v3/2d4f413e764740ef89d5596033efec3f"
      "https://mainnet.infura.io/v3/98ece994f154497b83e67c270eaf8381"
    )
  );

  const contract = new web3.eth.Contract(abi, mainnetContract);
  const address = (await getAccounts())[0];

  const transactionParameters = {
    to: mainnetContract,
    from: address,
    value: 0,
    data: contract.methods.adoptNLadyApes(startIndex, numberToClaim).encodeABI(),
  };

  try {
    await (window)?.ethereum?.request({
      method: "eth_sendTransaction",
      params: [transactionParameters],
    });
    return true;
  } catch (error) {
    console.log(error);
  }
}

export async function mintApes(numberToMint) {
	if (!window.ethereum) return;
  var web3 = new Web3(
    new Web3.providers.HttpProvider(
      // "https://rinkeby.infura.io/v3/2d4f413e764740ef89d5596033efec3f"
      "https://mainnet.infura.io/v3/98ece994f154497b83e67c270eaf8381"
    )
  );

  const aos = new web3.eth.Contract(aosAbi, aosContract);
  const address = (await getAccounts())[0];

  const transactionParameters = {
    to: mainnetContract,
    from: address,
    value: parseInt(web3.utils.toWei((numberToMint * 0.1).toFixed(3).toString(), "ether")).toString(16),
    data: aos.methods.mintApes(numberToMint).encodeABI(),
  };

  try {
    await (window)?.ethereum?.request({
      method: "eth_sendTransaction",
      params: [transactionParameters],
    });
    return true;
  } catch (error) {
    console.log(error);
  }
} 
