import React from 'react';

import { LazyLoadImage } from 'react-lazy-load-image-component';

import partner1 from '../../assets/images/partner1.png';
import partner1_2x from '../../assets/images/partner1@2x.png';
import partner2 from '../../assets/images/partner2.png';
import partner2_2x from '../../assets/images/partner2@2x.png';
import partner3 from '../../assets/images/partner3.png';
import partner3_2x from '../../assets/images/partner3@2x.png';
import partner4 from '../../assets/images/partner4.png';
import partner4_2x from '../../assets/images/partner4@2x.png';

import red from '../../assets/images/red-el.svg';
import blue_crystall from '../../assets/images/blue-crystall-el.svg';
import green from '../../assets/images/green-el.svg';

const partnersArr = [
  { id: 1, link: '', img: { '1x': partner1, '2x': partner1_2x } },
  { id: 2, link: '', img: { '1x': partner2, '2x': partner2_2x } },
  { id: 3, link: '', img: { '1x': partner3, '2x': partner3_2x } },
  { id: 4, link: '', img: { '1x': partner4, '2x': partner4_2x } },
];

function OfficialPartners() {
  return (
    <div className="partners">
      <div className="partners-wrapper">
        <img data-img="red-1" src={red} alt="" />
        <img data-img="red-2" src={red} alt="" />
        <img data-img="blue-1" src={blue_crystall} alt="" />
        <img data-img="green-1" src={green} alt="" />
        <div className="container">
          <h2 className="title title_size-m partners-title">Official partners</h2>
          <div className="partners-block">
            {partnersArr.map(({ id, link, img }) => (
              <a className="partners-item" href={link} key={id}>
                <LazyLoadImage width="80" height="80" src={img['1x']} srcSet={`${img['1x']} 1x, ${img['2x']} 2x`} alt="" />
              </a>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default OfficialPartners;
