/* eslint-disable */

import React, { useState } from 'react';
import Header from '../../components/header';
import red from '../../assets/images/red-el.svg';
import green from '../../assets/images/green-el.svg';
import blue_crystall from '../../assets/images/blue-crystall-el.svg';
import OfficialPartners from '../../components/official-partners';
import MintModal from '../../components/mint-modal';
import mint_monkey from '../../assets/images/aos-3d-art-whitelist.png'; 
import mint_monkey_2x from '../../assets/images/aos-3d-art-whitelist@2x.png';

function Claim() {
	const [availableApes, setAvailableApes] = useState(100);
	const [initialApes, setInitialApes] = useState(1000);
	const [nftPerWallet, setNftPerWallet] = useState(1);
	const [claimMintPrice, setClaimMintPrice] = useState('0.00');

	const [isModalActive, setIsModalActive] = useState(false);

	// Event handlers
	const handleInputChange = () => {
		return;
	}

	const handleMintBtn = () => {
		const htmlEl = document.documentElement;
		
		htmlEl.classList.add('is-locked');

		setIsModalActive(true);
	};

	const currentModalProps = {
		title: 'You can buy an AoS token on OpenSea and get your own 3D token',
		subtitle: '',
		btnText: 'Go to opensea',
		btnLink: 'https://opensea.io/collection/apesofspace-official'
	}

  return (
    <div className="mint claim">
			<Header />
			<div className="mint-content">
				<div className="mint-content__wrapper">
					<img data-img="red-1" src={red} alt="" />
        	<img data-img="blue-1" src={blue_crystall} alt="" />
					<img data-img="mint-monkey" src={mint_monkey} srcSet={`${mint_monkey} 1x, ${mint_monkey_2x} 2x`} alt="" />
					<div className="mint-content__block">
						<img data-img="green-1" src={green} alt="" />
						<img data-img="blue-2" src={blue_crystall} alt="" />
						<h1 className="title title_size-l mint-content__title">apes of space vx minted: {availableApes}/{initialApes}</h1>
						<p className="title title_size-m mint-content__subtitle">Claim Mint price: {claimMintPrice} ETH (free)</p>
						<div className="mint-content__count">
							<input 
								type="text" 
								className="mint-input mint-content__count-input" 
								name="apes_count" 
								value={`${nftPerWallet} NFT per wallet`}
								onChange={evt => handleInputChange(evt)}
							></input>
						</div>
						<div className="mint-content__btn-wrapper">
							<button type="button" className="btn btn_yellow mint-content__btn" 				onClick={handleMintBtn}>
								Claim aos vx
							</button>
						</div>
						<div className="mint-content__note">
							<p>
								There are many variations of passages of Lorem Ipsum available, but the majority have 
								suffered alteration in some form, by&nbsp;injected humour, or randomised words which don't 
								look even slightly believable. If you are going to&nbsp;use&nbsp;a&nbsp;passage&nbsp;of&nbsp;Lorem&nbsp;Ipsum.
							</p>
						</div>
					</div>
				</div>
			</div>
			<OfficialPartners />
			<MintModal 
				isModalActive={isModalActive} 
				setIsModalActive={setIsModalActive} 
				currentModalProps={currentModalProps} 
			/>
		</div>
  );
}

export default Claim;
