import React from 'react';

function MintModal({ isModalActive, setIsModalActive, currentModalProps }) {
  const handleModalClose = () => {
    const htmlEl = document.documentElement;

    htmlEl.classList.remove('is-locked');

    setIsModalActive(false);
  };

  return (
    <>
      <div className={`mint-modal ${isModalActive && 'mint-modal_active'}`}>
        <div className="mint-modal__wrapper">
          <div className="mint-modal__close-wrapper">
            <button type="button" className="mint-modal__close" onClick={handleModalClose}>
              <span />
              <span />
            </button>
          </div>
          <h2 className="title title_item-l mint-modal__title">{currentModalProps.title}</h2>
          {currentModalProps.subtitle && <span className="title title_item mint-modal__subtitle">{currentModalProps.subtitle}</span>}
          <div className="mint-modal__btn-wrapper">
            <a href={currentModalProps.btnLink} type="button" className="btn btn_yellow mint-modal__btn">
              {currentModalProps.btnText}
            </a>
          </div>
        </div>
      </div>
      <div className={`back-blur back-blur_violet ${isModalActive && 'back-blur_active'}`} />
    </>
  );
}

export default MintModal;
